html,
body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  background: transparent;
  font-family: 'OpenSans', Arial, Helvetica, sans-serif;
  font-weight: 400;
  color: #444;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}
* {
  box-sizing: border-box;
}
